@font-face {
    font-family: 'VG';
    src: local('VG'), url(../fonts/game.ttf) format('truetype');
    font-display: swap;
}
:root{
    --green: #39904c;
    --world-shadow: -0.4rem 0.4rem 0.3rem 0 rgb(40 40 40 / 35%);
    --world-shadow-color: rgb(40 40 40 / 35%);
    --character-shadow: -6px 9px 14px 8px rgb(0 0 0 / 30%);
    --transparent-white: #ffffffdf; /* d7f4fedd */
}
body{
    font-family: "Lucida Console","VG";
    background-color: var(--green);
    cursor: url(data:image/png;base64,), auto;
}
a, button, .ui.button, #player-controls{
    cursor: url(data:image/png;base64,) 14 0, pointer;
}
#mawmawmaw {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    filter: blur(.4px);
}
h1,h2,h3,h4,p{
    font-family: 'VG';
    font-weight: 100;
    color: #111;
}
a{
    font-weight: bold;
    color: #111;
}
h1{
    font-size: 34px;
}
h2{
    font-size: 18px;
}
h3{
    font-size: 12px;
}
p, ul li, a{
    font-size: 10.5px;
}
#player-controls {
    font-family: 'VG';
    position: fixed;
    bottom: 5px;
    right: 5px;
    background-color: rgba(0,0,0,0.6);
    padding: 10px;
    color: white;
    border-radius: 4px;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: bold;
    display: none;
}
div#player-controls img {
    display: inherit;
    width: 100px;
    margin: 0 auto;
}
.blue,.primary{
    background-color: #003fc7db !important;
}
.blue:hover,.primary:hover{
    background-color: #0437a5db !important;
}
.button, input, textarea {
    border-style: solid !important;
    border-width: 4px !important;
    border-image-slice: 2 !important;
    border-image-width: 3 !important;
    border-image-repeat: space !important;
    border-image-outset: 2 !important;
    background-clip: padding-box !important;
}
i.spinner.icon {
    animation: loading 1s linear 0s infinite;
    font-size: 64px;
    line-height: 64px;
    color: #333;
}
@keyframes loading {
    0%   {transform: rotate(0deg)}
    100% {transform: rotate(360deg)}
}
@media screen and (max-width: 768px){
    #mawmawmaw {
        height: auto;
        min-height: 100%;
    }
    div#player-controls img {
        display: none;
    }
    .ui.container.main {
        margin: 5px 20px !important;
    }
}
@media screen and (min-width:768px) and (max-width: 992px){
    h1{
        font-size: 28px;
    }
    h2{
        font-size: 20px;
    }
    h3{
        font-size: 14px;
    }
    p{
        font-size: 10px;
    }
}